h1 {
  text-align: center;
  padding: 10px;
  margin-bottom: 45px;
}
.card {
  border: 1px solid #ddd;
  float: left;
  position: relative;
  width: 20%;
  margin: 10px 2.5% 50px 2.5%;
}

.card .stats {
  height: 60px;
}
.card img {
  width: 100%;
  margin-bottom: 0;
  vertical-align: middle;
}

.card:hover {
  -webkit-box-shadow: 0px 5px 10px #888888;
          box-shadow: 0px 5px 10px #888888;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.title {
  text-align: center;
}

.title p {
  margin: 15px auto;
  -webkit-transform: translateY(15px);
      -ms-transform: translateY(15px);
          transform: translateY(15px);
  width: 100%;
}

.card:hover .overLay,
.card.comparing .overLay {
  position: absolute;
  top: 0;
  left: 0;
  height: calc(100% - 75.5px);
  background-color: #302e2e;
  opacity: 0.7;
  width: 100%;
  opacity: 0.5;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

span {
  text-transform: uppercase;
  text-align: center;
  display: inline-block;
  padding: 0.7em;
  border: 1px solid white;
  position: absolute;
  top: 30%;
  font-size: 1em;
  color: white;
}

.card:hover span.compare_button,
.card:hover span.remove_button {
  display: inline-block;
}

span:hover {
  background: none;
  -webkit-box-shadow: 0px 2px 10px white;
          box-shadow: 0px 2px 10px white;
}

.notAnimation {
  font-size: 20px;
  left: 22%;
  visibility: hidden;
  width: 60%;
  -webkit-transform: translateY(-40px);
      -ms-transform: translateY(-40px);
          transform: translateY(-40px);
  -webkit-transition: linear 210ms;
  -o-transition: linear 210ms;
  transition: linear 210ms;
}
.animation {
  left: 25%;
  width: 50%;
  display: inline;
  -webkit-transform: translateY(40px);
      -ms-transform: translateY(40px);
          transform: translateY(40px);
  -webkit-transition: all 200ms ease-out;
  -o-transition: all 200ms ease-out;
  transition: all 200ms ease-out;
}



